body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.pace {
  -webkit-pointer-events: none;
  pointer-events: none;

  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
      user-select: none;
}

.pace-inactive {
  display: none;
}

.pace .pace-progress {
  background: #29d;
  position: fixed;
  z-index: 2000;
  top: 0;
  right: 100%;
  width: 100%;
  height: 2px;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #192233;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.op {
    border-top: 3px solid white;
    border-bottom: 3px solid white;
    background-color: rgba(255, 238, 82, 0.479);
}

.link-posts {
    display: flex;
    flex-wrap: wrap;
    
}

.link-posts .child {
    flex: 1 1 21%;
    margin: 1rem;

}
button {
    margin-right: 1rem;
}
.nsfw Button {
    color: lightsalmon;
    display: block;
}
.post {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    
    
}

.biz-card {
    width: 80%;
}

#timeline .vis-item {
    border-color: chartreuse;
    color: white;
}
#timeline .vis-item.vis-selected {
    color: #50267d;
}

#timeline .vis-item.vis-selected {
    border-color: #ff3d00;
}

#timeline .vis-time-axis .vis-grid.vis-odd {
    background: #15302d;
  }

@-webkit-keyframes fade-in {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes fade-in {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.post-image {
    width: 80%;
    height: 15rem;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
    -webkit-animation: fade-in 0.3s ease;
            animation: fade-in 0.3s ease;
}
.linear-progress {
    width: 80%;
}
.post-text {
    width: 40%;
    height: 400px;
    overflow-y: scroll;
}

.Preamble {
    font-size: 1rem;
}
.Preamble button {
    margin-bottom: 2rem;
    margin-right: 2rem;
}
